// See https://docs.github.com/articles/workflow-syntax-for-github-actions
const regularStepKeys = ['uses', 'with']
const runStepKeys = ['run', 'working-directory', 'shell']

const inputsTypeMap: Map<string, string> = new Map()

const validateStepKey = async (workflow: Workflow, path: string[]): Promise<Validation | undefined> => {
  const lastPath = path[path.length - 1]!
  const arrFrom = regularStepKeys.includes(lastPath) ? regularStepKeys : runStepKeys
  const arrForbidden = arrFrom === regularStepKeys ? runStepKeys : regularStepKeys

  const keys = keysForPath(workflow, path.slice(0, path.length - 1))
  const conflicting = arrForbidden.filter(key => keys.includes(key))
  if (conflicting.length === 0) return

  return {
    message: html` ${codeTag(lastPath)} cannot be used with ${joinCodeTags(conflicting)} `,
    severity: 'warning',
  }
}

const regularJobKeys = [
  'concurrency',
  'container',
  'continue-on-error',
  'defaults',
  'env',
  'environment',
  'if',
  'name',
  'needs',
  'runs-on',
  'outputs',
  'permissions',
  'services',
  'steps',
  'strategy',
  'timeout-minutes',
]
const callableWorkflowJobKeys = [
  'if',
  'name',
  'needs',
  'permissions',
  'secrets',
  'uses',
  'with',
  'concurrency',
  'strategy',
]

const canUseCallableWorkflow = () => {
  return true
}

const canValidateOutputKey = () => {
  return featureFlag.isFeatureEnabled('ACTIONS_MATRIX_OUTPUT_IMPROVEMENTS')
}

const canShowActionsGreenTreeKey = () => {
  return featureFlag.isFeatureEnabled('ACTIONS_GREEN_TREES')
}

const canUseNumberTypeDispatchInputs = () => {
  return featureFlag.isFeatureEnabled('ACTIONS_NUMBER_TYPE_DISPATCH_INPUTS')
}

const validateJobKey = async (workflow: Workflow, path: string[]): Promise<Validation | undefined> => {
  if (!canUseCallableWorkflow()) return
  const currentKey = path[path.length - 1]!
  if (regularJobKeys.includes(currentKey) !== callableWorkflowJobKeys.includes(currentKey)) {
    // This key is unique to one of the types of jobs
    const otherKeys = keysForPath(workflow, path.slice(0, path.length - 1)).filter(key => key !== currentKey)
    // Get any other present keys unique to the other set
    const conflicting = otherKeys.filter(
      key =>
        regularJobKeys.includes(key) !== callableWorkflowJobKeys.includes(key) &&
        regularJobKeys.includes(key) !== regularJobKeys.includes(currentKey),
    )

    if (conflicting.length === 0) return

    return {
      message: html` You can't use ${codeTag(currentKey)} with ${joinCodeTags(conflicting)} `,
      severity: 'error',
    }
  }
}

const validateOutputKey = async (workflow: Workflow, path: string[]): Promise<Validation | undefined> => {
  if (!canValidateOutputKey()) return

  const currentKey = path[path.length - 1]!
  if (isExpressionFunction(currentKey)) return
  const contexts = getExpressionContexts(currentKey)
  const nonMatrixContexts = contexts.filter(x => !x.startsWith('matrix.'))
  if (nonMatrixContexts.length === 0) return

  return {
    message: html` Only ${codeTag(`matrix`)} context can be used in output key expression, invalid contexts found:
    ${nonMatrixContexts.join(', ')}`,
    severity: 'error',
  }
}

const validateReusableOutputKey = async (workflow: Workflow, path: string[]): Promise<Validation | undefined> => {
  if (!canValidateOutputKey()) return

  const currentKey = path[path.length - 1]!
  if (isExpressionFunction(currentKey)) return
  const contexts = getExpressionContexts(currentKey)
  const nonInputContexts = contexts.filter(x => !x.startsWith('inputs.'))

  if (nonInputContexts.length === 0) return

  return {
    message: html` Only ${codeTag(`inputs`)} context can be used in output key expression, invalid contexts found:
    ${nonInputContexts.join(', ')}`,
    severity: 'error',
  }
}

const primitiveTypes = ['string', 'number', 'boolean']

// Any event that has a Rule beyond `{ nullable: true }`
const irregularEvents = [
  'merge_group',
  'push',
  'pull_request',
  'pull_request_target',
  'schedule',
  'workflow_call',
  'workflow_run',
  'workflow_dispatch',
]

const regularEvents = [
  'branch_protection_rule',
  'check_run',
  'check_suite',
  'commit_comment',
  'create',
  'delete',
  'deployment',
  'deployment_status',
  'discussion',
  'discussion_comment',
  'fork',
  'gollum',
  'issue_comment',
  'issues',
  'label',
  'member',
  'milestone',
  'package',
  'page_build',
  'project',
  'project_card',
  'project_column',
  'public',
  'pull_request_review_comment',
  'pull_request_review',
  'registry_package', // will be deprecated
  'repository_dispatch',
  'release',
  'status',
  'watch',
]

const allEvents = irregularEvents.concat(regularEvents)

const arrayOrSingleString: Rule = {
  single: ['string'],
  items: {
    single: ['string'],
  },
}

const arrayOrSingleGlob: Rule = {
  validateValue: validateGlob,
  single: ['string'],
  items: {
    validateValue: validateGlob,
    single: ['string'],
  },
}

const codeEventRules = (): Rule => {
  const rules: Rule = {
    nullable: true,
    rules: {
      branches: arrayOrSingleGlob,
      'branches-ignore': arrayOrSingleGlob,
      tags: arrayOrSingleGlob,
      'tags-ignore': arrayOrSingleGlob,
      paths: arrayOrSingleGlob,
      'paths-ignore': arrayOrSingleGlob,
      types: arrayOrSingleString,
    },
  }

  if (canShowActionsGreenTreeKey()) {
    rules.rules = {
      ...rules.rules,
      'reuse-previous-outcome': {
        nullable: false,
        single: ['boolean'],
      },
    }
  }

  return rules
}

const mergeGroupRule = (): Rule => {
  const mgRules: Rule = {
    nullable: true,
    rules: {
      branches: arrayOrSingleGlob,
      'branches-ignore': arrayOrSingleGlob,
    },
  }

  if (canShowActionsGreenTreeKey()) {
    mgRules.rules = {
      ...mgRules.rules,
      'reuse-previous-outcome': {
        nullable: false,
        single: ['boolean'],
      },
    }
  }

  return mgRules
}

function buildWorkflowDispatchRules() {
  onRules['workflow_dispatch'] = {
    async validateValue(workflow: Workflow, path: string[], value: unknown): Promise<Validation | undefined> {
      if (value !== null) {
        const helpUrl =
          document.getElementById('workflow-dispatch-help-url')?.getAttribute('data-help-url') ||
          'https://docs.github.com'
        return {
          // prettier-ignore
          message: html` ${codeTag('workflow_dispatch')} only supports mapping values. <a class="Link--inTextBlock" href="${helpUrl}" target="_blank">Learn more</a> `,
          severity: 'error',
        }
      }
    },
    nullable: true,
    rules: {
      inputs: {
        description: 'Inputs for a `workflow_dispatch` event',
        keyPlaceholder: 'input_name',
        keyPlaceholderHelp: 'Enter the name for the input',
        map: {
          nullable: true,
          rules: {
            type: {
              description: "Type of this input, defaults to 'text'",
              single: ['string'],
              values: canUseNumberTypeDispatchInputs()
                ? ['text', 'choice', 'boolean', 'number']
                : ['text', 'choice', 'boolean'],
            },
            options: {
              description: "Options for an input of type 'choice'",
              multipleValues: true,
              items: {
                single: ['string'],
              },
            },
            required: {
              description: 'Whether this input needs to be specified when running the workflow',
              single: ['boolean'],
              values: ['true', 'false'],
            },
            description: {
              description: 'Displays a custom description instead of the name',
              single: ['string'],
            },
            default: {
              async validateValue(workflow: Workflow, path: string[], value: unknown): Promise<Validation | undefined> {
                const type = findNode(workflow, [...path.slice(0, path.length - 1), 'type'])
                if (!type) return
                if (type === 'boolean') {
                  if (
                    (typeof value === 'string' && (value === 'true' || value === 'false')) ||
                    (typeof value === 'boolean' && (value === true || value === false))
                  ) {
                    return
                  }
                  return {
                    message: html`
                      ${codeTag('type')} is set to ${codeTag(type)} but ${codeTag('default')} has invalid value
                    `,
                    severity: 'error',
                  }
                }
                if (canUseNumberTypeDispatchInputs() && type === 'number') {
                  if (typeof value === 'number') {
                    return
                  }
                  return {
                    message: html`
                      ${codeTag('type')} is set to ${codeTag(type)} but ${codeTag('default')} has invalid value
                    `,
                    severity: 'error',
                  }
                }
              },
              description: 'Default value for this input',
              single: canUseNumberTypeDispatchInputs() ? primitiveTypes : ['string', 'boolean'],
            },
          },
        },
      },
    },
  }
}

const onRules: Rules = {
  merge_group: mergeGroupRule(),
  push: codeEventRules(),
  ['pull_request']: codeEventRules(),
  pull_request_target: codeEventRules(),
  schedule: {
    items: {
      rules: {
        cron: {
          single: ['string'],
          validateValue: async (
            workflow: Workflow,
            path: string[],
            value: unknown,
          ): Promise<Validation | undefined> => {
            const explain = prettyCron(String(value))
            if (!explain) {
              return {
                message: html` Unrecognized cron expression `,
                severity: 'warning',
              }
            }
            const scheduleUrl = document
              .getElementById('workflow-on-schedule-url')!
              .getAttribute('data-on-schedule-url')!

            return {
              message: html`
                Runs ${explain}. <br />
                Actions schedules run at most every 5 minutes using UTC time.
                <a class="Link--inTextBlock" href="${scheduleUrl}" target="_blank">Learn more</a>
              `,
              severity: 'info',
            }
          },
        },
      },
    },
  },
  workflow_run: {
    nullable: true,
    rules: {
      branches: arrayOrSingleGlob,
      'branches-ignore': arrayOrSingleGlob,
      workflows: arrayOrSingleString,
      types: arrayOrSingleString,
    },
  },
}

function buildCallableWorkflowRules() {
  onRules['workflow_call'] = {
    nullable: true,
    rules: {
      inputs: {
        description: 'Inputs for a `workflow_call` event',
        keyPlaceholder: 'input_name',
        keyPlaceholderHelp: 'Enter the name for the input',
        map: {
          rules: {
            description: {
              description: 'Displays a custom description instead of the name',
              single: ['string'],
            },
            type: {
              async validateValue(workflow: Workflow, path: string[], value: unknown): Promise<Validation | undefined> {
                if (typeof value !== 'string' || !primitiveTypes.includes(value)) {
                  return {
                    message: html` ${codeTag('type')} must be one of: ${joinCodeTags(primitiveTypes)} `,
                    severity: 'error',
                  }
                }
              },
              description: 'The type of value callers can pass for this input',
              single: ['string'],
              values: primitiveTypes,
              required: true,
            },
            required: {
              description: 'Whether this input needs to be specified when calling the workflow',
              single: ['boolean'],
              values: ['true', 'false'],
            },
            default: {
              async validateValue(workflow: Workflow, path: string[], value: unknown): Promise<Validation | undefined> {
                const type = findNode(workflow, [...path.slice(0, path.length - 1), 'type'])
                if (!type) return
                if (type === 'string') return // We allow numbers and booleans, coerce them into strings
                // eslint-disable-next-line valid-typeof
                if (typeof value !== type) {
                  return {
                    message: html`
                      ${codeTag('type')} is set to ${codeTag(type)}, but ${codeTag('default')} is of type
                      ${codeTag(typeof value)}
                    `,
                    severity: 'error',
                  }
                }
              },
              description: 'Default value for this input',
              single: primitiveTypes,
            },
          },
        },
      },
      outputs: {
        description: 'Outputs for a `workflow_call` event',
        keyPlaceholder: 'output_name',
        keyPlaceholderHelp: 'Enter the name for the output',
        map: {
          validateKey: validateReusableOutputKey,
          rules: {
            description: {
              description: 'Displays a custom description instead of the name',
              single: ['string'],
            },
            value: {
              description: 'The value callers can receive from this output',
              single: primitiveTypes,
            },
          },
        },
      },
      secrets: {
        description: 'Inputs passed as secrets for a `workflow_call` event',
        keyPlaceholder: 'secret_name',
        keyPlaceholderHelp: 'Enter the name for the secret',
        map: {
          rules: {
            description: {
              description: 'Displays a custom description instead of the name',
              single: ['string'],
            },
            required: {
              description: 'Whether this secret needs to be specified when calling the workflow',
              single: ['boolean'],
              values: ['true', 'false'],
            },
          },
        },
      },
    },
  }
}

/*
 * In order to support something like this:
 *
 * on:
 *   issue_comment:
 *   push:
 *     branches: master
 *
 * We need to accept any event also as a key, even if it's empty
 */
for (const eventName of regularEvents) {
  onRules[eventName] = {
    nullable: true,
  }
}

