const buildSecretsRule = (): Rule => {
  if (canUseCallableWorkflow()) {
    return {
      description:
        'A map of the secret parameters defined by the reusable workflow. Use `inherit` to pass all secrets to the referenced workflow',
      validateKey: validateJobKey,
      async validateValue(workflow: Workflow, path: string[], value: unknown): Promise<Validation | undefined> {
        if (typeof value !== 'string') return

        if (value !== 'inherit') {
          return {
            message: html`Use ${codeTag('inherit')} to pass all secrets to the called workflow.`,
            severity: 'error',
          }
        }
      },
      single: ['string'],
      map: {
        single: primitiveTypes,
      },
    }
  } else {
    return {
      description: 'A map of the secret parameters defined by the reusable workflow',
      validateKey: validateJobKey,
      map: {
        single: primitiveTypes,
      },
    }
  }
}

const buildRunsOnRule = (): Rule => {
  return {
    validateKey: validateJobKey,
    description: 'The type of virtual host machine to run the job on',
    required: !canUseCallableWorkflow(),
    multipleValues: true,
    caseInsensitive: true,
    single: ['string'],
    items: {
      single: ['string'],
    },
    rules: {
      group: {
        description: 'The runner group name to target the runner group',
        single: ['string'],
      },
      labels: {
        description: 'The type of virtual host machine to run the job on',
        multipleValues: true,
        caseInsensitive: true,
      },
    },
  }
}

// Includes rules that may be controlled by feature flags
function fullRootRule(): RootRule {
  return {
    suggestionsForExpression,
    processExpression,
    rules: {
      name: {
        description: 'The name of your workflow',
        single: ['string'],
      },
      'run-name': {
        description: 'The name of the run',
        single: ['string'],
      },
      defaults: {
        description: 'A set of workflow defaults that will apply to all jobs',
        rules: {
          run: {
            description: 'Set default behavior for all `run` steps within workflow',
            rules: {
              shell: {
                description: 'Set default `shell` for all `run` steps within workflow',
                single: ['string'],
              },
              'working-directory': {
                description: 'Set default `working-directory` for all `run` steps within workflow',
                single: ['string'],
              },
            },
          },
        },
      },
      on: {
        description: 'The name of the GitHub event that triggers the workflow',
        required: true,
        values: allEvents,
        multipleValues: true,
        rules: onRules,
      },
      concurrency: {
        description: 'The concurrency for this job',
        single: ['string'],
        rules: {
          group: {
            description: 'The name of the concurrency group to wait for',
            single: ['string'],
            required: true,
          },
          'cancel-in-progress': {
            description: 'If new run should cancel in progress runs',
            single: ['boolean'],
            required: false,
          },
        },
      },
      env: {
        description: 'A map of environment variables that are available to all jobs and steps in the workflow',
        map: {single: primitiveTypes},
      },
      jobs: {
        description: 'A workflow run is made up of one or more jobs',
        keyPlaceholder: 'job_id',
        keyPlaceholderHelp: 'Enter the id for the new job',
        map: {
          async validateKey(workflow: Workflow, path: string[]): Promise<Validation | undefined> {
            const currentJobId = path[1]!.toLowerCase()

            if (!/^[a-z_]/i.test(currentJobId)) {
              return {
                message: html` Job id must start with a letter or ${codeTag('_')} `,
                severity: 'error',
              }
            }
            if (!/^[\w-]+$/i.test(currentJobId)) {
              return {
                message: html` Job id must contain only alphanumeric characters, ${codeTag('-')}, or ${codeTag('_')} `,
                severity: 'error',
              }
            }

            const jobIds = keysForPath(workflow, path.slice(0, path.length - 1)).map(k => k.toLowerCase())

            const uniqueIds = jobIds.filter(jobId => jobId === currentJobId)
            if (uniqueIds.length > 1) {
              return {
                message: html` This job id is already used in another job `,
                severity: 'error',
              }
            }

            const jobKeys = keysForPath(workflow, path)
            if (canUseCallableWorkflow() && !jobKeys.includes('runs-on') && !jobKeys.includes('uses')) {
              return {
                message: html` A job requires either ${codeTag('runs-on')} or ${codeTag('uses')} `,
                severity: 'error',
              }
            }
          },
          rules: {
            if: {
              description: 'You can use the `if` conditional to prevent a job from running unless a condition is met',
              single: ['string', 'boolean'],
            },
            env: {
              validateKey: validateJobKey,
              description: 'A map of environment variables that are available to all steps in the job',
              map: {single: primitiveTypes},
            },
            name: {
              validateKey: validateJobKey,
              description: 'The name of the job displayed on GitHub',
              single: ['string'],
              async validateValue(workflow: Workflow, path: string[], value: unknown): Promise<Validation | undefined> {
                if (typeof value !== 'string') return
                const jobs = findNode(workflow, ['jobs'])
                if (!jobs || typeof jobs !== 'object') return // should never happen
                for (const jobId in jobs) {
                  if (jobId === path[1]) continue // ignore current job
                  const job = jobs[jobId]
                  if (typeof job === 'object' && job !== null && (job.name || jobId) === value) {
                    return {
                      message: html` This job name is already used in another job `,
                      severity: 'error',
                    }
                  }
                }
              },
            },
            outputs: {
              validateKey: validateJobKey,
              description: 'Declare outputs from this job',
              map: {
                single: primitiveTypes,
                validateKey: validateOutputKey,
              },
            },
            defaults: {
              validateKey: validateJobKey,
              description: 'A set of job defaults that will apply to all steps',
              rules: {
                run: {
                  description: 'Set default behavior for all `run` steps within job',
                  rules: {
                    shell: {
                      description: 'Set default `shell` for all `run` steps within job',
                      single: ['string'],
                    },
                    'working-directory': {
                      description: 'Set default `working-directory` for all `run` steps within job',
                      single: ['string'],
                    },
                  },
                },
              },
            },
            concurrency: {
              validateKey: validateJobKey,
              description: 'The concurrency for this job',
              single: ['string'],
              rules: {
                group: {
                  description: 'The name of the concurrency group to wait for',
                  single: ['string'],
                  required: true,
                },
                'cancel-in-progress': {
                  description: 'If new run should cancel in progress runs',
                  single: ['boolean'],
                  required: false,
                },
              },
            },
            environment: {
              validateKey: validateJobKey,
              description: 'The environment for this job',
              single: ['string'],
              rules: {
                name: {
                  description: 'The name of the environment',
                  single: ['string'],
                  required: true,
                },
                url: {
                  description: 'Set the URL for accessing your environment',
                  single: ['string'],
                  required: false,
                },
              },
            },
            needs: {
              description: 'Identifies any jobs that must complete successfully before this job will run',
              multipleValues: true,
              async values(workflow: unknown, path: string[]) {
                // path[1] is the current job id
                return keysForPath(workflow, ['jobs']).filter(str => str !== path[1])
              },
              async validateValue(workflow: Workflow, path: string[], value: unknown): Promise<Validation | undefined> {
                if (typeof value !== 'string') return
                // @ts-expect-error values could be an array which we don't handle
                const values = await this.values(workflow, path)
                if (!values.includes(value)) {
                  if (value === path[1]) {
                    return {
                      message: html` A job cannot depend on itself `,
                      severity: 'error',
                    }
                  }
                  return {
                    message: html` Unknown job ${codeTag(value)} `,
                    severity: 'error',
                  }
                }
              },
            },
            'runs-on': buildRunsOnRule(),
            steps: {
              validateKey: validateJobKey,
              description: 'The sequence of tasks to run in this job',
              items: {
                rules: {
                  id: {
                    description: 'A unique identifier for the step',
                    single: ['string'],
                    async validateValue(
                      workflow: Workflow,
                      path: string[],
                      value: unknown,
                    ): Promise<Validation | undefined> {
                      if (typeof value !== 'string') return
                      let steps = findNode(workflow, path.slice(0, 3))
                      if (!Array.isArray(steps)) return
                      steps = steps.slice()
                      steps.splice(+path[3]!, 1) // remove current step
                      // eslint-disable-next-line @typescript-eslint/no-explicit-any
                      if (steps.find((step: any) => typeof step === 'object' && step !== null && step.id === value)) {
                        return {
                          message: html` This step id is already used in another step `,
                          severity: 'error',
                        }
                      }
                    },
                  },
                  if: {
                    description: 'Prevents the step from running unless the condition is met',
                    single: ['string', 'boolean'],
                  },
                  name: {
                    description: 'The name to display on GitHub',
                    single: ['string'],
                  },
                  uses: {
                    description: 'Selects an action to run as part of a step in your job',
                    validateKey: validateStepKey,
                    single: ['string'],
                    async validateValue(
                      workflow: Workflow,
                      path: string[],
                      value: unknown,
                    ): Promise<Validation | undefined> {
                      if (typeof value !== 'string') return

                      // If it's a Docker action or a local action, just go ahead
                      // and return.
                      if (value.startsWith('docker://') || value.startsWith('./')) return

                      const [_, version] = value.split('@')

                      // If version doesn't exist, throw an error
                      if (!version) {
                        return {
                          message: html` Missing version for action `,
                          severity: 'error',
                        }
                      }

                      // If the version looks like a short sha, warn about the upcoming deprecation
                      if (/\b[0-9a-f]{7,8}\b/i.test(version)) {
                        const helpUrl = document
                          .getElementById('workflow-short-sha-warning-url')!
                          .getAttribute('data-help-url')!

                        return {
                          message: html`
                            The ref ${codeTag(version)} looks like a short sha. This is not supported, please check the
                            <a class="Link--inTextBlock" href="${helpUrl}" target="_blank">documentation</a>.
                          `,
                          severity: 'warning',
                        }
                      }
                    },
                  },
                  run: {
                    description: "Runs command line programs using the operating system's shell",
                    validateKey: validateStepKey,
                    single: ['string'],
                  },
                  shell: {
                    description: "Overrides the default shell settings in the virtual environment's operating system",
                    validateKey: validateStepKey,
                    single: ['string'],
                    validateValue: async (
                      workflow: Workflow,
                      path: string[],
                      value: unknown,
                    ): Promise<Validation | undefined> => {
                      const all = ['bash', 'pwsh', 'python']
                      const win = ['cmd', 'powershell']
                      const unix = ['sh']

                      if (typeof value !== 'string') {
                        return
                      }

                      if ([...all, ...unix, ...win].includes(value)) {
                        return
                      } else if (typeof value === 'string' && value.match(/^\S+ +\{0\}$/i)) {
                        return
                      }

                      const helpUrl = document.getElementById('workflow-shell-help-url')!.getAttribute('data-help-url')
                      return {
                        message: html`Invalid value. Please check the
                          <a class="Link--inTextBlock" href="${helpUrl}" target="_blank">documentation</a>.`,
                        severity: 'error',
                      }
                    },
                  },
                  with: {
                    description: 'A map of the input parameters defined by the action',
                    validateKey: validateStepKey,
                    map: {
                      single: primitiveTypes,
                    },
                  },
                  env: {
                    description: 'Sets environment variables for steps to use in the virtual environment',
                    map: {
                      single: primitiveTypes,
                    },
                  },
                  'working-directory': {
                    description: "The default directory that the action uses in a job's workspace",
                    validateKey: validateStepKey,
                    single: ['string'],
                  },
                  'continue-on-error': {
                    description: 'Prevents a job from failing when a step fails',
                    single: ['boolean'],
                    values: ['true', 'false'],
                  },
                  'timeout-minutes': {
                    description: 'The maximum number of minutes to run the step before killing the process',
                    single: ['number'],
                  },
                },
              },
            },
            'timeout-minutes': {
              validateKey: validateJobKey,
              description: 'The maximum number of minutes to let a workflow run before GitHub automatically cancels it',
              single: ['number'],
            },
            'continue-on-error': {
              validateKey: validateJobKey,
              description: 'Prevents a workflow run from failing when a job fails',
              single: ['boolean'],
              values: ['true', 'false'],
            },
            strategy: {
              validateKey: validateJobKey,
              description: 'A strategy creates a build matrix for your jobs',
              rules: {
                matrix: {
                  description: 'Set of different configurations of the virtual environment',
                  map: {
                    items: {
                      single: primitiveTypes,
                      map: {
                        // for config, exclude and include options
                        single: primitiveTypes,
                      },
                    },
                    async validateKey(workflow: Workflow, path: string[]): Promise<Validation | undefined> {
                      const value = findNode(workflow, path)
                      if (typeof value !== 'object' || !Array.isArray(value)) return

                      const specialKeys = ['config', 'exclude', 'include']
                      const key = path[path.length - 1]!

                      if (specialKeys.includes(key)) {
                        if (value.length === 0) return
                        if (typeof value[0] !== 'object')
                          return {
                            message: html` ${joinCodeTags(specialKeys)} must be an array of objects `,
                            severity: 'error',
                          }
                      } else {
                        if (value.length === 0) {
                          return {
                            message: html` ${key} must contain at least one value `,
                            severity: 'error',
                          }
                        }
                        if (typeof value[0] === 'object')
                          return {
                            message: html` Matrix options must only contain primitive values `,
                            severity: 'error',
                          }
                      }
                    },
                  },
                },
                'fail-fast': {
                  description: 'When set to true, GitHub cancels all in-progress jobs if any matrix job fails',
                  single: ['boolean'],
                  values: ['true', 'false'],
                },
                'max-parallel': {
                  description: 'The maximum number of jobs that can run simultaneously',
                  single: ['number'],
                },
              },
            },
            container: {
              validateKey: validateJobKey,
              description: "A container to run any steps in a job that don't already specify a container",
              single: ['string'],
              rules: serviceAndContainerRules,
            },
            services: {
              validateKey: validateJobKey,
              description: 'Additional containers to host services for a job in a workflow',
              map: {
                single: ['string'],
                rules: serviceAndContainerRules,
              },
            },
            permissions: {
              description: 'Control permissions for runnings jobs in a workflow',
              single: ['string'],
              values: ['read-all', 'write-all'],
              rules: permissionRules,
            },
            uses: {
              validateKey: validateJobKey,
              description: 'Run a reusable workflow as a job',
              single: ['string'],
              async validateValue(workflow: Workflow, path: string[], value: unknown): Promise<Validation | undefined> {
                if (typeof value !== 'string') return

                if (!value.includes('.github/workflows/') && !value.includes('.github/workflows-lab/')) {
                  return {
                    message: html` Missing the full path for reusable workflow `,
                    severity: 'error',
                  }
                }

                const [_, version] = value.split('@')

                // If version doesn't exist and it's not a local workflow, throw an error
                if (!version && !value.startsWith('./')) {
                  return {
                    message: html` Missing version for reusable workflow `,
                    severity: 'error',
                  }
                } else if (version && value.startsWith('./')) {
                  return {
                    message: html` Cannot call a local reusable workflow with a version `,
                    severity: 'error',
                  }
                } else if (version === '' && value.startsWith('./')) {
                  return {
                    message: html` Invalid path to local reusable workflow file (@) `,
                    severity: 'error',
                  }
                }

                // If the version looks like a short sha, warn about the upcoming deprecation
                if (/\b[0-9a-f]{7,8}\b/i.test(version!)) {
                  const helpUrl = document
                    .getElementById('workflow-short-sha-warning-url')!
                    .getAttribute('data-help-url')!

                  return {
                    message: html`
                      The ref ${codeTag(version!)} looks like a short sha. This is not supported, please check the
                      <a class="Link--inTextBlock" href="${helpUrl}" target="_blank">documentation</a>.
                    `,
                    severity: 'warning',
                  }
                }
              },
            },
            secrets: buildSecretsRule(),
            with: {
              description: 'A map of the input parameters defined by the reusable workflow',
              validateKey: validateJobKey,
              map: {
                single: primitiveTypes,
              },
            },
          },
        },
      },
      permissions: {
        description: 'Control permissions for runnings jobs in a workflow',
        single: ['string'],
        values: ['read-all', 'write-all'],
        rules: permissionRules,
      },
    },
  }
}

function mapType(type: string) {
  return (
    {number: 'integer', string: 'string', boolean: 'boolean', environment: 'string', choice: 'string'}[type] ||
    'invalid'
  )
}

function setupInputsType(yaml: Record<string, Record<string, Record<string, unknown>>>) {
  inputsTypeMap.clear()

  const setupInputs = (inputs: Record<string, Record<string, unknown>>) => {
    for (const [name, value] of Object.entries(inputs)) {
      if (!inputsTypeMap.has(name)) {
        let type = 'string'
        if (value.type) {
          type = mapType(value.type as string)
        } else if (value.default) {
          type = mapType(typeof value.default)
        }
        inputsTypeMap.set(name, type)
      }
    }
  }

  if (yaml && yaml.on) {
    if (yaml.on.workflow_dispatch && yaml.on.workflow_dispatch.inputs) {
      setupInputs(yaml.on.workflow_dispatch.inputs as Record<string, Record<string, unknown>>)
    }

    if (yaml.on.workflow_call && yaml.on.workflow_call.inputs) {
      setupInputs(yaml.on.workflow_call.inputs as Record<string, Record<string, unknown>>)
    }
  }
}

// Removes any excluded rules
export function getRootRule(yaml: unknown): RootRule {
  const rule = fullRootRule()

  buildWorkflowDispatchRules()
  if (canUseCallableWorkflow()) {
    buildCallableWorkflowRules()
  }

  setupInputsType(yaml as Record<string, Record<string, Record<string, unknown>>>)

  if (!canUseCallableWorkflow() && rule.rules) {
    if (rule.rules.jobs!.map?.rules) {
      delete rule.rules.jobs!.map.rules.uses
      delete rule.rules.jobs!.map.rules.secrets
      delete rule.rules.jobs!.map.rules.with
    }
  }

  return rule
}

type Workflow = unknown

// See https://docs.github.com/articles/contexts-and-expression-syntax-for-github-actions#contexts
const rootContext: Context = {
  keys: {
    github: {
      keys: {
        event: {},
        workflow: {},
        actor: 'string',
        repository: 'string',
        ['event_name']: 'string',
        sha: 'string',
        ref: 'string',
        ['head_ref']: 'string',
        ['base_ref']: 'string',
      },
    },
    job: {
      keys: {
        status: 'string',
        container: {
          keys: {
            network: 'string',
            id: 'string',
          },
        },
        services: {
          keys: {
            network: 'string',
          },
          map: {
            keys: {
              id: 'string',
              ports: {
                map: {
                  type: 'number',
                },
              },
            },
          },
          async resolver(workflow: Workflow, path: string[]): Promise<string[]> {
            return keysForPath(workflow, ['jobs', path[1]!, 'services'])
          },
        },
      },
    },
    steps: {
      map: {
        keys: {
          outputs: {
            map: {
              type: 'string',
            },
          },
        },
      },
      async resolver(workflow: Workflow, path: string[]): Promise<string[]> {
        const steps = findNode(workflow, ['jobs', path[1]!, 'steps']) || []
        if (!Array.isArray(steps)) return []
        return steps.map(step => (step && typeof step === 'object' && step.id ? String(step.id) : '')).filter(Boolean)
      },
    },
    runner: {},
    secrets: {
      keys: {
        GITHUB_TOKEN: 'string',
      },
      map: {
        type: 'string',
      },
      // TODO: implement a resolver here that returns secret names from the repository
    },
    matrix: {
      map: {
        type: 'string',
      },
      async resolver(workflow: Workflow, path: string[]) {
        return keysForPath(workflow, ['jobs', path[1]!, 'strategy', 'matrix'])
      },
    },
  },
}

function pathSupportsInlineExpressions(path: string[]) {
  if (path[0] !== 'jobs') return false
  if (path.length < 2) return false
  if (path[path.length - 1] === 'if') return false
  return true
}

async function suggestionsForContext(
  contextPath: string[],
  workflow: Workflow,
  path: string[],
): Promise<ContextSuggestion[] | null> {
  const keys = contextPath.slice(0)
  let obj: Context = rootContext
  while (keys.length > 0) {
    const key = unescapeContext(keys.shift()!)
    if (obj.keys && obj.keys[key]) {
      if (typeof obj.keys[key] !== 'string') {
        obj = obj.keys[key]
      } else {
        return null
      }
    } else if (keys.length > 0) {
      return null
    }
  }
  if (obj.resolver) {
    const values = await obj.resolver(workflow, path)
    // All values from resolvers are terminal by now
    return values.map(text => ({text, terminal: true}))
  }
  if (obj.keys) {
    const contextKeys = obj.keys
    return Object.keys(contextKeys).map(text => ({text, terminal: typeof contextKeys[text] === 'string'}))
  }
  return null
}

function escapeContext(name: string, first: boolean): string {
  return name.match(/^[a-z][a-z0-9_-]+?$/i) ? `${first ? '' : '.'}${name}` : `['${name}']`
}

const endOfExpression = "([a-z0-9_\\-.[\\]']+)?$"
const anyChars = '(?:.+?)?'
const expressionStart = '\\${{'

const ifExpression = new RegExp(endOfExpression, 'i')
const inlineExpression = new RegExp(`${expressionStart}${anyChars}${endOfExpression}`, 'i')

async function suggestionsForExpression(
  workflow: Workflow,
  path: string[],
  lastValue: string,
): Promise<Suggestion[] | null> {
  if (pathSupportsInlineExpressions(path)) {
    // Syntax doc here https://docs.github.com/articles/contexts-and-expression-syntax-for-github-actions#contexts
    // There are two syntaxes: index syntax (e.g. github['sha']) and dereference syntax (e.g. github.sha)
    // "${{"" followed by optional whitespaces, then followed by optional identifiers (a-Z, 0-9) separated by (dots or brackets with single quotes)
    const expression = lastValue.match(inlineExpression)
    if (expression) return suggestionsFromExpression(workflow, path, expression[1] || '')
  } else if (path[path.length - 1] === 'if') {
    const expression = lastValue.match(ifExpression)
    if (expression) return suggestionsFromExpression(workflow, path, expression[1] || '')
  }
  return null
}

async function processExpression(
  workflow: unknown,
  path: string[],
  expression: string,
  rule: Rule,
): Promise<Validation | undefined> {
  let acceptedTypes: string[] = []
  if (rule.single) {
    acceptedTypes = rule.single
  } else if (rule.values) {
    if (Array.isArray(rule.values)) {
      acceptedTypes = rule.values.map(value => mapType(typeof value))
    } else {
      acceptedTypes = (await rule.values(rule, path)).map(value => mapType(typeof value))
    }
  } else if (rule.map || rule.rules) {
    acceptedTypes.push('object')
  }

  if (acceptedTypes.includes('object')) {
    if (isFromJsonExpression(expression)) return
    if (acceptedTypes.length === 1 && isStringWithExpressions(expression)) {
      return unexpectedTypeMessage(acceptedTypes, codeTag('string'))
    }
  }

  const inputsExpressions = extractInputsExpressions(expression)
  for (const inputExpression of inputsExpressions) {
    const expressionSplit = inputExpression.split('.')
    const inputName = expressionSplit[1]!.trim()
    const inputType = inputsTypeMap.get(inputName)

    if (inputType && !acceptedTypes.includes(inputType)) {
      return unexpectedTypeMessage(acceptedTypes, codeTag(inputType))
    }
  }
}

interface ExpressionFunctionArgument {
  name: string
}

interface ExpressionFunction {
  name: string
  description: string
  args: ExpressionFunctionArgument[]
  jobFunction: boolean
}

function createFunction(name: string, description: string, args: ExpressionFunctionArgument[]) {
  return {name, description, args, jobFunction: false}
}

function createJobFunction(name: string, description: string) {
  return {name, description, args: [], jobFunction: true}
}

// See https://docs.github.com/articles/contexts-and-expression-syntax-for-github-actions#functions
const expressionFunctions: ExpressionFunction[] = [
  // eslint-disable-next-line i18n-text/no-en
  createFunction('contains', 'Returns true if the searchString contains searchValue', [
    {name: 'searchString'},
    {name: 'searchValue'},
  ]),
  // eslint-disable-next-line i18n-text/no-en
  createFunction('startsWith', 'Returns true when searchString starts with searchValue', [
    {name: 'searchString'},
    {name: 'searchValue'},
  ]),
  // eslint-disable-next-line i18n-text/no-en
  createFunction('endsWith', 'Returns true if searchString ends with searchValue', [
    {name: 'searchString'},
    {name: 'searchValue'},
  ]),
  // eslint-disable-next-line i18n-text/no-en
  createFunction('format', 'Replaces values in the string, with the variable replaceValueN', [
    {name: 'string'},
    {name: 'replaceValueN'},
  ]),
  // eslint-disable-next-line i18n-text/no-en
  createFunction('join', 'All values in element are concatenated into a string', [
    {name: 'element'},
    {name: 'optionalElem'},
  ]),
  // eslint-disable-next-line i18n-text/no-en
  createFunction('toJson', 'Returns a pretty-print JSON representation of value', [{name: 'value'}]),
  // eslint-disable-next-line i18n-text/no-en
  createFunction('hashFiles', 'Returns a single hash for the set of files that matches the path pattern', [
    {name: 'path'},
  ]),
  // eslint-disable-next-line i18n-text/no-en
  createJobFunction('success', 'Returns true when the previous step of a job succeeds'),
  // eslint-disable-next-line i18n-text/no-en
  createJobFunction('always', 'Forces a conditional to evaluate as true, even when canceled'),
  // eslint-disable-next-line i18n-text/no-en
  createJobFunction('cancelled', 'Returns true if the workflow was canceled'),
  // eslint-disable-next-line i18n-text/no-en
  createJobFunction('failure', 'Returns true when the previous step of a job fails'),
]

/*
 * Autocompletes expressions
 */
async function suggestionsFromExpression(
  workflow: Workflow,
  path: string[],
  expressionContent: string,
): Promise<Suggestion[]> {
  const contextPath = expressionContent.split(/(?=\[)|(?=\.)/) // split by dots or opening brackets but don't consume any char
  const replace = contextPath[contextPath.length - 1]!
  const lastPath = unescapeContext(contextPath[contextPath.length - 1]!)
  const values: ContextSuggestion[] = (await suggestionsForContext(contextPath, workflow, path)) || []
  const filteredContexts = filterValues(values, lastPath)
  const suggestions: Suggestion[] = filteredContexts.map(context => ({
    displayText: context.text,
    snippet: {
      tokens: [escapeContext(context.text, contextPath.length === 1)],
      keepCursorLocation: !context.terminal,
    },
    replace,
  }))

  if (contextPath.length < 2) {
    // If there are no dots in the part of the expression being written, we also suggest function names
    const lowerCasedLastPath = lastPath.toLowerCase()
    const canUseJobFunctions = path[path.length - 1] === 'if'
    for (const fun of expressionFunctions) {
      if (fun.jobFunction && !canUseJobFunctions) continue
      const lowerCasedName = fun.name.toLowerCase()
      if (!lowerCasedLastPath || startsWithAndNotEquals(lowerCasedLastPath, lowerCasedName)) {
        const tokens = []
        if (fun.args.length > 0) {
          tokens.push(`${fun.name}(`)
          for (const arg of fun.args) {
            if (tokens.length > 1) {
              tokens.push(', ')
            }
            tokens.push(arg.name)
          }
          tokens.push(')')
        } else {
          tokens.push(`${fun.name}()`)
        }
        suggestions.push({
          displayText: `${fun.name}(${fun.args.length > 0 ? '' : ''})`,
          description: fun.description,
          snippet: {tokens},
          replace,
        })
      }
    }
  }
  return suggestions
}
