/*
 * Parses a valid cron expression and produces a valid schedule that
 * can then be used with Later.
 *
 * CronParser().parse('5 * * * *', true);
 *
 * @param {String} expr: The cron expression to parse
 * @api public
 */

type Schedule = {
  [ket: string]: number[]
}

interface Schedules {
  schedules: Schedule[]
  exceptions: unknown[]
}

export default function parseCron(expr: string) {
  // Constant array to convert valid names to values
  const NAMES = {
    JAN: 1,
    FEB: 2,
    MAR: 3,
    APR: 4,
    MAY: 5,
    JUN: 6,
    JUL: 7,
    AUG: 8,
    SEP: 9,
    OCT: 10,
    NOV: 11,
    DEC: 12,
    SUN: 1,
    MON: 2,
    TUE: 3,
    WED: 4,
    THU: 5,
    FRI: 6,
    SAT: 7,
  }

  interface Field {
    // Contains the index, min, max, and offset for each of the constraints
    constraints: [number, number, number, number]
    // launch uses the robfig/cron Go package for parsing. It allows a subset of characters,
    // see https://pkg.go.dev/github.com/robfig/cron?utm_source=godoc
    // It allows ? to be used identically to *
    allowedSyntax: RegExp
  }

  interface Fields {
    m: Field // minutes
    h: Field // hours
    D: Field // day of month
    M: Field // month
    d: Field // day of week
  }

  // Contains the index, min, and max for each of the constraints
  const FIELDS: Fields = {
    m: {constraints: [0, 0, 59, 0], allowedSyntax: /^[0-9,*/?-]+$/},
    h: {constraints: [1, 0, 23, 0], allowedSyntax: /^[0-9,*/?-]+$/},
    D: {constraints: [2, 1, 31, 0], allowedSyntax: /^[0-9,*/?-]+$/}, // No L or W support
    M: {constraints: [3, 1, 12, 0], allowedSyntax: /^([0-9,*/?-]|JAN|FEB|MAR|APR|MAY|JUN|JUL|AUG|SEP|OCT|NOV|DEC)+$/},
    d: {constraints: [4, 1, 7, 1], allowedSyntax: /^([0-6,*/?-]|SUN|MON|TUE|WED|THU|FRI|SAT)+$/}, // No # or L support
  }

  /*
   * Returns the value + offset if value is a number, otherwise it
   * attempts to look up the value in the NAMES table and returns
   * that result instead.
   *
   * @param {Int,String} value: The value that should be parsed
   * @param {Int} offset: Any offset that must be added to the value
   */
  function getValue(value: number | string, offset = 0, max = 9999) {
    if (isNaN(value)) {
      return NAMES[value] || null
    }
    const num = +value + offset
    if (num < 0 || num > max) {
      throw new Error(`Number ${num} out of range [0, ${max}].`)
    }
    return num
  }

  /*
   * Adds values to the specified constraint in the current schedule.
   *
   * @param {Sched} sched: The schedule to add the constraint to
   * @param {String} name: Name of constraint to add
   * @param {Int} min: Minimum value for this constraint
   * @param {Int} max: Maximum value for this constraint
   * @param {Int} inc: The increment to use between min and max
   */
  function add(sched: Schedule, name: string, min: number, max: number, inc = 1) {
    if (!sched[name]) {
      sched[name] = []
    }

    for (let i = min; i <= max; i += inc) {
      if (sched[name].indexOf(i) < 0) {
        sched[name].push(i)
      }
    }

    sched[name].sort((a, b) => a - b)
  }

  /*
   * Adds a range item (of the form x-y/z) to the schedule.
   *
   * @param {String} item: The cron expression item to add
   * @param {Schedule} curSched: The current schedule to add to
   * @param {String} name: The name to use for this constraint
   * @param {Int} min: The min value for the constraint
   * @param {Int} max: The max value for the constraint
   * @param {Int} offset: The offset to apply to the cron value
   */
  function addRange(item: string, curSched: Schedule, name: string, min: number, max: number, offset: number) {
    // parse range/x
    const incSplit = item.split('/')

    const inc = +incSplit[1] || undefined

    const range = incSplit[0]

    // parse x-y or * or 0
    if (range !== '*' && range !== '0') {
      const rangeSplit = range.split('-')
      min = getValue(rangeSplit[0], offset, max)

      // fix for issue #13, range may be single digit
      max = getValue(rangeSplit[1], offset, max) || max
    }

    add(curSched, name, min, max, inc)
  }

  /*
   * Parses a particular item within a cron expression.
   *
   * @param {String} item: The cron expression item to parse
   * @param {Schedule} s: The existing set of schedules
   * @param {String} name: The name to use for this constraint
   * @param {Int} min: The min value for the constraint
   * @param {Int} max: The max value for the constraint
   * @param {Int} offset: The offset to apply to the cron value
   */
  function parse(item: string, s: Schedules, name: string, min: number, max: number, offset: number) {
    let value

    const schedules = s.schedules

    const curSched = schedules[schedules.length - 1]

    // parse x
    if ((value = getValue(item, offset, max)) !== null) {
      add(curSched, name, value, value)
    }
    // parse x-y or x-y/z or */z or 0/z
    else {
      addRange(item, curSched, name, min, max, offset)
    }
  }

  /*
   * Returns true if the expression components are valid
   *
   * @param {String[]} components: The expression components
   */
  function isValidComponents(components: string[]): boolean {
    if (components.length > Object.keys(FIELDS).length) {
      return false
    }

    for (let i = 0; i < components.length; i++) {
      const component = components[i]
      const validSyntax = Object.values<Field>(FIELDS).find(v => v.constraints[0] === i).allowedSyntax
      if (!component.match(validSyntax) || component.startsWith('/')) {
        return false
      }
    }

    return true
  }

  /*
   * Parses each of the fields in a cron expression.  The expression must
   * include the seconds field, the year field is optional.
   *
   * @param {String} expr: The cron expression to parse
   */
  function parseExpr(expression: string) {
    const components = expression.replace(/(\s)+/g, ' ').split(' ')
    if (!isValidComponents(components)) {
      return null
    }

    const schedule: Schedules = {schedules: [{}], exceptions: []}

    for (const field in FIELDS) {
      const f = FIELDS[field]
      const component = components[f.constraints[0]]
      if (component && component !== '*' && component !== '?') {
        const items = component.split(',')
        for (let i = 0; i < items.length; i++) {
          parse(items[i], schedule, field, f.constraints[1], f.constraints[2], f.constraints[3])
        }
      }
    }

    return schedule
  }

  try {
    return parseExpr(expr.toUpperCase())
  } catch {
    return null
  }
}
