import cronstrue from 'cronstrue'
import parse from './cron-parser'

type Options = {
  capitalize?: boolean
}

/*
 * Given a cronspec, return the human-readable string.
 */
export default function prettyCron(cronspec: string, {capitalize = false}: Options = {}): string | null {
  const schedule = parse(cronspec)
  if (!schedule) {
    return null
  }

  let desc = ''
  try {
    desc = cronstrue.toString(cronspec, {
      dayOfWeekStartIndexZero: true,
      monthStartIndexZero: false,
      use24HourTimeFormat: true,
      // cronstrue sets the description as the error if throwExceptionOnParseError is false
      // so we need to distinguish between an error and a valid description
      throwExceptionOnParseError: true,
    })
  } catch (err) {
    return null
  }

  if (!desc) {
    return null
  }

  // By default, cronstrue capitalizes the first letter of the description.
  if (capitalize) {
    return desc
  }

  return desc.charAt(0).toLowerCase() + desc.slice(1)
}
